import React, {Component} from 'react';

import './item-add-form.scss';

export default class ItemAddForm extends Component {
    state = {
        label: ''
    };

    onLabelChange = (e) => {
        this.setState({
            label: e.target.value
        });
    };

    onSubmit = (e) => {
        e.preventDefault();
        if (this.state.label.length !== 0) {
            this.props.onItemAdded(this.state.label);

            this.setState({
                label: ''
            });
        }
    };

    render() {
        return (
            <form className="item-add-form" onSubmit={this.onSubmit}>
                <div className="row">
                    <div className="col-9">
                        <input type="text" className="form-control" placeholder="Добавить todo" onChange={this.onLabelChange} value={this.state.label}/>
                    </div>
                    <div className="col-3">
                        <button type="submit" className="btn btn-block btn-outline-secondary">Add Item</button>
                    </div>
                </div>
            </form>
        );
    }
};
